<template>
  <div>
    <sec-btn @click="resetDateFilter"></sec-btn>
    <sec-btn @click="clearFilter"></sec-btn>
    <sec-table ref="filterTable" class="mt10" :data="tableData">
      <sec-table-column
        label=""
        prop="date"
        width="180"
        column-key="date"
        sortable
        :filters="[
          {text: '2016-05-01', value: '2016-05-01'},
          {text: '2016-05-02', value: '2016-05-02'},
          {text: '2016-05-03', value: '2016-05-03'},
          {text: '2016-05-04', value: '2016-05-04'},
        ]"
        :filter-method="filterHandler"
      ></sec-table-column>
      <sec-table-column label="" prop="address"></sec-table-column>
      <sec-table-column
        label=""
        width="100"
        filter-placement="bottom-end"
        :filters="[
          { text: '', value: '' },
          { text: '', value: '' },
        ]"
        :filter-method="filterTag"
      >
        <template #default="{ row }">
          <sec-tag :type="row.tag === '' ? 'primary' : 'success'">{{ row.tag }}</sec-tag>
        </template>
      </sec-table-column>
    </sec-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 ',
        tag: '',
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1517 ',
        tag: '',
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519 ',
        tag: '',
      }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516 ',
        tag: '',
      }],
    };
  },
  methods: {
    resetDateFilter() {
      this.$refs.filterTable.clearFilter('date');
    },
    clearFilter() {
      this.$refs.filterTable.clearFilter();
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    filterHandler(value, row, column) {
      const { property } = column;
      return row[property] === value;
    },
  },
};
</script>
